// js/core/MPAInitializer.js - MPAÂàùÊúüÂåñ„Çπ„ÇØ„É™„Éó„Éà

import { authManager } from '../modules/authManager.js';
import { supabaseService } from '../services/supabaseService.js';
import { showNotification } from '../utils/helpers.js';
import { handleError } from '../utils/errorHandler.js';
import { tooltipManager } from '../utils/TooltipManager.js';

/**
 * MPAÂàùÊúüÂåñ„ÇØ„É©„Çπ
 * ÂêÑ„Éö„Éº„Ç∏„ÅßÂÖ±ÈÄö„ÅÆÂàùÊúüÂåñÂá¶ÁêÜ„ÇíÂÆüË°å
 *
 * @class MPAInitializer
 * @version 2.0.0
 * @since 1.0.0
 */
class MPAInitializer {
  /**
   * MPAÂàùÊúüÂåñ„ÇØ„É©„Çπ„ÅÆ„Ç≥„É≥„Çπ„Éà„É©„ÇØ„Çø
   * @param {Object} options - ÂàùÊúüÂåñ„Ç™„Éó„Ç∑„Éß„É≥
   * @param {boolean} options.autoInitialize - Ëá™ÂãïÂàùÊúüÂåñÔºà„Éá„Éï„Ç©„É´„Éà: trueÔºâ
   * @param {boolean} options.enablePerformanceMonitoring - „Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÁõ£Ë¶ñÔºà„Éá„Éï„Ç©„É´„Éà: trueÔºâ
   */
  constructor(options = {}) {
    this.isInitialized = false;
    this.currentPage = this.getCurrentPageName();
    this.autoInitialize = options.autoInitialize !== false;
    this.enablePerformanceMonitoring =
      options.enablePerformanceMonitoring !== false;
    this.initializationTime = null;
    this.componentLoadTimes = new Map();
    this.errorCount = 0;
    this.maxRetries = 3;
    this.retryDelay = 1000;
  }

  /**
   * ÁèæÂú®„ÅÆ„Éö„Éº„Ç∏Âêç„ÇíÂèñÂæó
   */
  getCurrentPageName() {
    const path = window.location.pathname;
    const pageMap = {
      '/': 'dashboard',
      '/index.html': 'dashboard',
      '/workout.html': 'workout',
      '/calendar.html': 'calendar',
      '/analysis.html': 'analysis',
      '/progress.html': 'progress',
      '/muscle-rotation-manager/progress.html': 'progress',
      '/exercises.html': 'exercises',
      '/settings.html': 'settings',
      '/help.html': 'help',
      '/privacy.html': 'privacy',
      // GitHub PagesÁî®„ÅÆ„Éë„Çπ
      '/muscle-rotation-manager/': 'dashboard',
      '/muscle-rotation-manager/index.html': 'dashboard',
      '/muscle-rotation-manager/workout.html': 'workout',
      '/muscle-rotation-manager/calendar.html': 'calendar',
      '/muscle-rotation-manager/analysis.html': 'analysis',
      '/muscle-rotation-manager/exercises.html': 'exercises',
      '/muscle-rotation-manager/settings.html': 'settings',
      '/muscle-rotation-manager/help.html': 'help',
      '/muscle-rotation-manager/privacy.html': 'privacy',
    };
    return pageMap[path] || 'dashboard';
  }

  /**
   * MPAÂàùÊúüÂåñ„ÇíÂÆüË°å
   * @param {Object} options - ÂàùÊúüÂåñ„Ç™„Éó„Ç∑„Éß„É≥
   * @param {boolean} options.force - Âº∑Âà∂ÂàùÊúüÂåñÔºà„Éá„Éï„Ç©„É´„Éà: falseÔºâ
   * @param {boolean} options.skipAuth - Ë™çË®º„ÉÅ„Çß„ÉÉ„ÇØ„Çí„Çπ„Ç≠„ÉÉ„ÉóÔºà„Éá„Éï„Ç©„É´„Éà: falseÔºâ
   * @returns {Promise<boolean>} ÂàùÊúüÂåñÊàêÂäü„Åã„Å©„ÅÜ„Åã
   */
  async initialize(options = {}) {
    if (this.isInitialized && !options.force) {
      console.log('‚ö†Ô∏è MPA already initialized, skipping...');
      return true;
    }

    console.log(`üöÄ Initializing MPA for page: ${this.currentPage}`);
    const startTime = performance.now();
    this.errorCount = 0;

    try {
      // 1. Ë™çË®ºÁÆ°ÁêÜ„ÅÆÂàùÊúüÂåñ
      await this.initializeAuthManager();

      // 2. „ÉÑ„Éº„É´„ÉÅ„ÉÉ„Éó„Éû„Éç„Éº„Ç∏„É£„Éº„ÅÆÂàùÊúüÂåñ
      await this.initializeTooltipManager();

      // 3. Supabase„ÅÆÂàùÊúüÂåñ„ÇíÂæÖ„Å§
      await this.waitForSupabaseInitialization();

      // 3. Ë™çË®ºÁä∂ÊÖã„ÅÆÁ¢∫Ë™çÔºà„Çπ„Ç≠„ÉÉ„Éó„Ç™„Éó„Ç∑„Éß„É≥„Åå„Å™„ÅÑÂ†¥ÂêàÔºâ
      if (!options.skipAuth) {
        await this.checkAuthentication();
      }

      // 3. ÂÖ±ÈÄö„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÅÆË™≠„ÅøËæº„Åø
      await this.loadCommonComponents();

      // 4. „Éö„Éº„Ç∏Âõ∫Êúâ„ÅÆÂàùÊúüÂåñ
      await this.initializePageSpecific();

      // 5. „Ç§„Éô„É≥„Éà„É™„Çπ„Éä„Éº„ÅÆË®≠ÂÆö
      this.setupEventListeners();

      // 6. „Ç®„É©„Éº„Éè„É≥„Éâ„É™„É≥„Ç∞„ÅÆË®≠ÂÆö
      this.setupErrorHandling();

      // 7. „Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÁõ£Ë¶ñ„ÅÆË®≠ÂÆö
      if (this.enablePerformanceMonitoring) {
        this.setupPerformanceMonitoring();
      }

      this.initializationTime = performance.now() - startTime;
      this.isInitialized = true;

      console.log(
        `‚úÖ MPA initialization complete (${this.initializationTime.toFixed(2)}ms)`
      );

      // ÂàùÊúüÂåñÂÆå‰∫Ü„Ç§„Éô„É≥„Éà„ÇíÁô∫ÁÅ´
      this.dispatchEvent('mpaInitialized', {
        page: this.currentPage,
        initTime: this.initializationTime,
        componentLoadTimes: Object.fromEntries(this.componentLoadTimes),
      });

      return true;
    } catch (error) {
      this.errorCount++;
      console.error(
        `‚ùå MPA initialization failed (attempt ${this.errorCount}):`,
        error
      );

      // „É™„Éà„É©„Ç§„É≠„Ç∏„ÉÉ„ÇØ
      if (this.errorCount < this.maxRetries) {
        console.log(
          `üîÑ Retrying MPA initialization in ${this.retryDelay}ms...`
        );
        await this.delay(this.retryDelay);
        return await this.initialize({ ...options, force: true });
      }

      this.handleInitializationError(error);
      return false;
    }
  }

  /**
   * Ë™çË®ºÁä∂ÊÖã„Çí„ÉÅ„Çß„ÉÉ„ÇØ
   */
  async checkAuthentication() {
    try {
      // Supabase„ÅåÂà©Áî®ÂèØËÉΩ„Åã„ÉÅ„Çß„ÉÉ„ÇØ
      if (!supabaseService.isAvailable()) {
        console.log('üîê Supabase not available, skipping authentication check');
        return true; // Supabase„ÅåÂà©Áî®„Åß„Åç„Å™„ÅÑÂ†¥Âêà„ÅØË™çË®º„ÉÅ„Çß„ÉÉ„ÇØ„Çí„Çπ„Ç≠„ÉÉ„Éó
      }

      const isAuthenticated = await authManager.isAuthenticated();
      const currentUser = await authManager.getCurrentUser();

      console.log('üîê Authentication check:', {
        isAuthenticated,
        user: currentUser?.email || 'anonymous',
      });

      if (!isAuthenticated) {
        this.showLoginPrompt();
        return false;
      }

      return true;
    } catch (error) {
      console.error('‚ùå Authentication check failed:', error);
      this.showLoginPrompt();
      return false;
    }
  }

  /**
   * „É≠„Ç∞„Ç§„É≥„Éó„É≠„É≥„Éó„Éà„ÇíË°®Á§∫
   */
  showLoginPrompt() {
    const mainContent = document.getElementById('main-content');
    if (mainContent) {
      mainContent.innerHTML = `
                <div class="min-h-screen flex items-center justify-center bg-gray-50">
                    <div class="max-w-md w-full space-y-8">
                        <div class="text-center">
                            <h2 class="mt-6 text-3xl font-extrabold text-gray-900">
                                „É≠„Ç∞„Ç§„É≥„ÅåÂøÖË¶Å„Åß„Åô
                            </h2>
                            <p class="mt-2 text-sm text-gray-600">
                                „Åì„ÅÆ„Éö„Éº„Ç∏„Å´„Ç¢„ÇØ„Çª„Çπ„Åô„Çã„Å´„ÅØ„É≠„Ç∞„Ç§„É≥„Åó„Å¶„Åè„Å†„Åï„ÅÑ
                            </p>
                        </div>
                        <div class="mt-8 space-y-6">
                            <div class="space-y-4">
                                <button onclick="window.location.href='index.html'"
                                        class="group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500">
                                    <i class="fas fa-home mr-2"></i>
                                    „Éõ„Éº„É†„Å´Êàª„Çã
                                </button>
                                <button data-action="login"
                                        class="group relative w-full flex justify-center py-2 px-4 border border-gray-300 text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500">
                                    <i class="fas fa-sign-in-alt mr-2"></i>
                                    „É≠„Ç∞„Ç§„É≥
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            `;
    }
  }

  /**
   * ÂÖ±ÈÄö„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÇíË™≠„ÅøËæº„Åø
   */
  async loadCommonComponents() {
    try {
      console.log('üîÑ Loading common components...');

      // „Éò„ÉÉ„ÉÄ„Éº„ÄÅ„Çµ„Ç§„Éâ„Éê„Éº„ÄÅ„Éï„ÉÉ„Çø„Éº„Çí‰∏¶Ë°åË™≠„ÅøËæº„Åø
      const [headerResult, sidebarResult, footerResult] =
        await Promise.allSettled([
          this.loadHeader(),
          this.loadSidebar(),
          this.loadFooter(),
        ]);

      // ÁµêÊûú„Çí„É≠„Ç∞Âá∫Âäõ
      console.log('Component loading results:', {
        header: headerResult.status,
        sidebar: sidebarResult.status,
        footer: footerResult.status,
      });

      // „Ç®„É©„Éº„Éè„É≥„Éâ„É™„É≥„Ç∞
      if (headerResult.status === 'rejected') {
        console.warn('Header loading failed:', headerResult.reason);
      }
      if (sidebarResult.status === 'rejected') {
        console.warn('Sidebar loading failed:', sidebarResult.reason);
      }
      if (footerResult.status === 'rejected') {
        console.warn('Footer loading failed:', footerResult.reason);
      }

      // „Éò„ÉÉ„ÉÄ„Éº„ÅåË™≠„ÅøËæº„Åæ„Çå„ÅüÂæå„Å´Ë™çË®º„Ç§„Éô„É≥„Éà„É™„Çπ„Éä„Éº„ÇíÂÜçË®≠ÂÆö
      if (headerResult.status === 'fulfilled') {
        console.log('üîÑ Re-setting up auth event listeners after header load');
        await authManager.setupEventListeners();

        // Ë™çË®ºUI„ÇíÊõ¥Êñ∞
        console.log('üîÑ Updating auth UI after header load');
        await authManager.updateAuthUI();
      }
    } catch (error) {
      console.error('‚ùå Failed to load common components:', error);
      throw error;
    }
  }

  /**
   * „Éò„ÉÉ„ÉÄ„Éº„ÇíË™≠„ÅøËæº„Åø
   */
  async loadHeader() {
    const headerContainer = document.getElementById('header-container');
    if (!headerContainer) {
      throw new Error('Header container not found');
    }

    try {
      const response = await fetch('partials/header.html');
      if (!response.ok) {
        throw new Error(`Failed to load header: ${response.status}`);
      }
      const headerHTML = await response.text();
      headerContainer.innerHTML = headerHTML;
      console.log('‚úÖ Header loaded successfully');
    } catch (error) {
      console.error('‚ùå Header loading failed:', error);
      throw error;
    }
  }

  /**
   * „Çµ„Ç§„Éâ„Éê„Éº„ÇíË™≠„ÅøËæº„Åø
   */
  async loadSidebar() {
    const sidebarContainer = document.getElementById('sidebar-container');
    if (!sidebarContainer) {
      throw new Error('Sidebar container not found');
    }

    try {
      const response = await fetch('partials/sidebar.html');
      if (!response.ok) {
        throw new Error(`Failed to load sidebar: ${response.status}`);
      }
      const sidebarHTML = await response.text();
      sidebarContainer.innerHTML = sidebarHTML;
      console.log('‚úÖ Sidebar loaded successfully');
    } catch (error) {
      console.error('‚ùå Sidebar loading failed:', error);
      throw error;
    }
  }

  /**
   * „Éï„ÉÉ„Çø„Éº„ÇíË™≠„ÅøËæº„Åø
   */
  async loadFooter() {
    const footerContainer = document.getElementById('footer-container');
    if (!footerContainer) {
      throw new Error('Footer container not found');
    }

    try {
      const response = await fetch('partials/footer.html');
      if (!response.ok) {
        throw new Error(`Failed to load footer: ${response.status}`);
      }
      const footerHTML = await response.text();
      footerContainer.innerHTML = footerHTML;
      console.log('‚úÖ Footer loaded successfully');
    } catch (error) {
      console.error('‚ùå Footer loading failed:', error);
      throw error;
    }
  }

  /**
   * „Éö„Éº„Ç∏Âõ∫Êúâ„ÅÆÂàùÊúüÂåñ
   */
  async initializePageSpecific() {
    try {
      console.log(
        `üîÑ Initializing page-specific functionality for: ${this.currentPage}`
      );

      // „Éö„Éº„Ç∏Âõ∫Êúâ„ÅÆJavaScript„É¢„Ç∏„É•„Éº„É´„ÇíÂãïÁöÑ„Ç§„É≥„Éù„Éº„Éà
      const pageModule = await this.loadPageModule(this.currentPage);

      if (pageModule && typeof pageModule.initialize === 'function') {
        console.log(
          `üîÑ Calling initialize for ${this.currentPage} page module`
        );
        await pageModule.initialize();
        console.log(
          `‚úÖ Page-specific initialization complete for: ${this.currentPage}`
        );
      } else {
        console.warn(
          `‚ö†Ô∏è No initialize method found for ${this.currentPage} page module`
        );
      }
    } catch (error) {
      console.warn(
        `‚ö†Ô∏è Page-specific initialization failed for ${this.currentPage}:`,
        error
      );
      // „Éö„Éº„Ç∏Âõ∫Êúâ„ÅÆÂàùÊúüÂåñ„Å´Â§±Êïó„Åó„Å¶„ÇÇ„Ç¢„Éó„É™„Ç±„Éº„Ç∑„Éß„É≥„ÅØÂãï‰Ωú„Åô„Çã
    }
  }

  /**
   * „Éö„Éº„Ç∏„É¢„Ç∏„É•„Éº„É´„ÇíÂãïÁöÑ„Ç§„É≥„Éù„Éº„Éà
   */
  async loadPageModule(pageName) {
    try {
      const moduleMap = {
        dashboard: () => import('../pages/dashboardPage.js'),
        workout: () => import('../pages/workoutPage.js'),
        calendar: () => import('../pages/calendarPage.js'),
        analysis: () => import('../pages/analysisPage.js'),
        progress: () => import('../pages/progressPage.js'),
        exercises: () => import('../pages/exercisePage.js'),
        settings: () => import('../pages/settingsPage.js'),
      };

      const moduleLoader = moduleMap[pageName];
      if (!moduleLoader) {
        console.warn(`No module loader found for page: ${pageName}`);
        return null;
      }

      const module = await moduleLoader();
      return module.default || module;
    } catch (error) {
      console.error(`‚ùå Failed to load page module for ${pageName}:`, error);
      return null;
    }
  }

  /**
   * „Ç§„Éô„É≥„Éà„É™„Çπ„Éä„Éº„ÇíË®≠ÂÆö
   */
  setupEventListeners() {
    console.log('üîÑ Setting up event listeners...');

    // „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥„É™„É≥„ÇØ„ÅÆ„ÇØ„É™„ÉÉ„ÇØ„Ç§„Éô„É≥„ÉàÔºàMPAÁî®Ôºâ
    document.addEventListener('click', (e) => {
      const navLink = e.target.closest('a[href]');
      if (navLink && navLink.getAttribute('href').startsWith('/')) {
        // MPA„Åß„ÅØ„Éá„Éï„Ç©„É´„Éà„ÅÆ„Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥„Çí‰ΩøÁî®
        const href = navLink.getAttribute('href');
        console.log('üîó Navigation link clicked:', href);
        // „Éá„Éï„Ç©„É´„Éà„ÅÆ„Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥„ÇíË®±ÂèØÔºàe.preventDefault()„ÇíÂâäÈô§Ôºâ
      }
    });

    // „É¢„Éê„Ç§„É´„É°„Éã„É•„Éº„ÅÆ„Ç§„Éô„É≥„Éà
    this.setupMobileMenu();

    // „Ç™„É≥„É©„Ç§„É≥/„Ç™„Éï„É©„Ç§„É≥Áä∂ÊÖã„ÅÆÁõ£Ë¶ñ
    this.setupOnlineStatusMonitoring();

    console.log('‚úÖ Event listeners setup complete');
  }

  /**
   * „É¢„Éê„Ç§„É´„É°„Éã„É•„Éº„ÇíË®≠ÂÆö
   */
  setupMobileMenu() {
    const mobileMenuBtn = document.getElementById('mobile-menu-btn');
    const mobileSidebar = document.getElementById('mobile-sidebar');
    const mobileSidebarClose = document.getElementById('mobile-sidebar-close');

    if (mobileMenuBtn && mobileSidebar) {
      mobileMenuBtn.addEventListener('click', () => {
        mobileSidebar.classList.add('open');
      });
    }

    if (mobileSidebarClose && mobileSidebar) {
      mobileSidebarClose.addEventListener('click', () => {
        mobileSidebar.classList.remove('open');
      });
    }

    // „É¢„Éê„Ç§„É´„Çµ„Ç§„Éâ„Éê„ÉºÂ§ñ„ÇØ„É™„ÉÉ„ÇØ„ÅßÈñâ„Åò„Çã
    document.addEventListener('click', (e) => {
      if (
        mobileSidebar &&
        !mobileSidebar.contains(e.target) &&
        !mobileMenuBtn?.contains(e.target)
      ) {
        mobileSidebar.classList.remove('open');
      }
    });
  }

  /**
   * „Ç™„É≥„É©„Ç§„É≥Áä∂ÊÖã„ÅÆÁõ£Ë¶ñ„ÇíË®≠ÂÆö
   */
  setupOnlineStatusMonitoring() {
    window.addEventListener('online', () => {
      console.log('üåê Online status restored');
      showNotification('„Ç™„É≥„É©„Ç§„É≥„Å´Âæ©Â∏∞„Åó„Åæ„Åó„Åü', 'success');
    });

    window.addEventListener('offline', () => {
      console.log('üì± Offline status detected');
      showNotification('„Ç™„Éï„É©„Ç§„É≥„Å´„Å™„Çä„Åæ„Åó„Åü', 'warning');
    });
  }

  /**
   * „Ç®„É©„Éº„Éè„É≥„Éâ„É™„É≥„Ç∞„ÇíË®≠ÂÆö
   */
  setupErrorHandling() {
    // „Ç∞„É≠„Éº„Éê„É´„Ç®„É©„Éº„Éè„É≥„Éâ„É™„É≥„Ç∞
    window.addEventListener('error', (event) => {
      console.error('‚ùå Global JavaScript error:', event.error);
      handleError(event.error, {
        context: 'Global Error Handler',
        showNotification: true,
      });
    });

    // Êú™Âá¶ÁêÜ„ÅÆPromiseÊãíÂê¶„Çí„Ç≠„É£„ÉÉ„ÉÅ
    window.addEventListener('unhandledrejection', (event) => {
      console.error('‚ùå Unhandled Promise Rejection:', event.reason);
      handleError(event.reason, {
        context: 'Unhandled Promise Rejection',
        showNotification: true,
      });
    });
  }

  /**
   * ÂàùÊúüÂåñ„Ç®„É©„Éº„ÇíÂá¶ÁêÜ
   */
  handleInitializationError(_error) {
    const mainContent = document.getElementById('main-content');
    if (mainContent) {
      mainContent.innerHTML = `
                <div class="min-h-screen flex items-center justify-center bg-gray-50">
                    <div class="max-w-md w-full space-y-8">
                        <div class="text-center">
                            <i class="fas fa-exclamation-triangle text-6xl text-red-500 mb-4"></i>
                            <h2 class="text-3xl font-extrabold text-gray-900">
                                ÂàùÊúüÂåñ„Ç®„É©„Éº
                            </h2>
                            <p class="mt-2 text-sm text-gray-600">
                                „Ç¢„Éó„É™„Ç±„Éº„Ç∑„Éß„É≥„ÅÆÂàùÊúüÂåñ„Å´Â§±Êïó„Åó„Åæ„Åó„Åü
                            </p>
                        </div>
                        <div class="mt-8 space-y-6">
                            <button onclick="window.location.reload()"
                                    class="group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500">
                                <i class="fas fa-redo mr-2"></i>
                                „Éö„Éº„Ç∏„ÇíÂÜçË™≠„ÅøËæº„Åø
                            </button>
                        </div>
                    </div>
                </div>
            `;
    }
  }

  /**
   * ÂàùÊúüÂåñÁä∂ÊÖã„ÇíÂèñÂæó
   */
  isReady() {
    return this.isInitialized;
  }

  /**
   * ÁèæÂú®„ÅÆ„Éö„Éº„Ç∏Âêç„ÇíÂèñÂæó
   */
  getCurrentPage() {
    return this.currentPage;
  }

  /**
   * Ë™çË®ºÁÆ°ÁêÜ„ÅÆÂàùÊúüÂåñ
   * @returns {Promise<void>}
   */
  async initializeAuthManager() {
    try {
      console.log('üîê Initializing auth manager...');
      await authManager.initialize();
      console.log('‚úÖ Auth manager initialized');
    } catch (error) {
      console.error('‚ùå Auth manager initialization failed:', error);
      throw error;
    }
  }

  /**
   * „ÉÑ„Éº„É´„ÉÅ„ÉÉ„Éó„Éû„Éç„Éº„Ç∏„É£„Éº„ÇíÂàùÊúüÂåñ
   */
  async initializeTooltipManager() {
    try {
      console.log('üí° Initializing tooltip manager...');
      tooltipManager.initialize();
      console.log('‚úÖ Tooltip manager initialized');
    } catch (error) {
      console.error('‚ùå Tooltip manager initialization failed:', error);
      // „ÉÑ„Éº„É´„ÉÅ„ÉÉ„Éó„ÅÆÂàùÊúüÂåñ„Å´Â§±Êïó„Åó„Å¶„ÇÇ„Ç¢„Éó„É™„Ç±„Éº„Ç∑„Éß„É≥„ÅØÂãï‰Ωú„Åô„Çã
    }
  }

  /**
   * Supabase„ÅÆÂàùÊúüÂåñÂÆå‰∫Ü„ÇíÂæÖ„Å§
   */
  async waitForSupabaseInitialization() {
    const maxWaitTime = 10000; // 10Áßí
    const checkInterval = 100; // 100ms
    let elapsedTime = 0;

    console.log('‚è≥ Waiting for Supabase initialization...');

    while (elapsedTime < maxWaitTime) {
      if (supabaseService.isAvailable()) {
        console.log('‚úÖ Supabase initialization confirmed');
        return true;
      }

      await new Promise((resolve) => setTimeout(resolve, checkInterval));
      elapsedTime += checkInterval;
    }

    console.warn(
      '‚ö†Ô∏è Supabase initialization timeout - proceeding without Supabase'
    );
    return false;
  }

  /**
   * „Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÁõ£Ë¶ñ„ÅÆË®≠ÂÆö
   */
  setupPerformanceMonitoring() {
    console.log('üìä Setting up performance monitoring...');

    // „Éö„Éº„Ç∏Ë™≠„ÅøËæº„ÅøÊôÇÈñì„ÅÆÁõ£Ë¶ñ
    window.addEventListener('load', () => {
      const loadTime = performance.now();
      console.log(`üìä Page load time: ${loadTime.toFixed(2)}ms`);

      this.dispatchEvent('performanceMetric', {
        metric: 'pageLoadTime',
        value: loadTime,
        timestamp: new Date().toISOString(),
      });
    });

    // „É°„É¢„É™‰ΩøÁî®Èáè„ÅÆÁõ£Ë¶ñÔºàÈñãÁô∫„É¢„Éº„Éâ„ÅÆ„ÅøÔºâ
    if (window.DEV_MODE && performance.memory) {
      setInterval(() => {
        const memory = performance.memory;
        const memoryInfo = {
          used: Math.round(memory.usedJSHeapSize / 1024 / 1024),
          total: Math.round(memory.totalJSHeapSize / 1024 / 1024),
          limit: Math.round(memory.jsHeapSizeLimit / 1024 / 1024),
        };

        console.log('üìä Memory usage:', memoryInfo);

        this.dispatchEvent('performanceMetric', {
          metric: 'memoryUsage',
          value: memoryInfo,
          timestamp: new Date().toISOString(),
        });
      }, 30000); // 30Áßí„Åî„Å®
    }

    console.log('‚úÖ Performance monitoring setup complete');
  }

  /**
   * „Ç´„Çπ„Çø„É†„Ç§„Éô„É≥„Éà„ÇíÁô∫ÁÅ´
   * @param {string} eventName - „Ç§„Éô„É≥„ÉàÂêç
   * @param {Object} detail - „Ç§„Éô„É≥„ÉàË©≥Á¥∞
   */
  dispatchEvent(eventName, detail = {}) {
    const event = new CustomEvent(eventName, {
      detail: {
        page: this.currentPage,
        timestamp: new Date().toISOString(),
        ...detail,
      },
    });
    window.dispatchEvent(event);
  }

  /**
   * ÈÅÖÂª∂ÂÆüË°å
   * @param {number} ms - ÈÅÖÂª∂ÊôÇÈñìÔºà„Éü„É™ÁßíÔºâ
   * @returns {Promise<void>}
   */
  delay(ms) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }

  /**
   * MPA„ÅÆÂÅ•ÂÖ®ÊÄß„ÉÅ„Çß„ÉÉ„ÇØ
   * @returns {Object} ÂÅ•ÂÖ®ÊÄß„ÉÅ„Çß„ÉÉ„ÇØÁµêÊûú
   */
  healthCheck() {
    const issues = [];

    if (this.errorCount > 3) {
      issues.push('High error count');
    }

    if (this.initializationTime && this.initializationTime > 10000) {
      issues.push('Slow initialization');
    }

    if (this.componentLoadTimes.size === 0) {
      issues.push('No components loaded');
    }

    return {
      isHealthy: issues.length === 0,
      issues,
      score: Math.max(0, 100 - issues.length * 25),
      metrics: {
        errorCount: this.errorCount,
        initializationTime: this.initializationTime,
        componentCount: this.componentLoadTimes.size,
      },
    };
  }

  /**
   * MPA„ÅÆÊúÄÈÅ©Âåñ„ÇíÂÆüË°å
   * @returns {Promise<void>}
   */
  async optimize() {
    console.log('üîß Optimizing MPA...');

    // „É°„É¢„É™‰ΩøÁî®Èáè„ÅÆÊúÄÈÅ©Âåñ
    if (window.gc) {
      window.gc();
    }

    // ‰∏çË¶Å„Å™„Ç§„Éô„É≥„Éà„É™„Çπ„Éä„Éº„ÅÆÂâäÈô§
    this.cleanup();

    console.log('‚úÖ MPA optimization complete');
  }

  /**
   * „ÇØ„É™„Éº„É≥„Ç¢„ÉÉ„ÉóÂá¶ÁêÜ
   */
  cleanup() {
    console.log('üßπ Cleaning up MPA...');

    // „Ç§„Éô„É≥„Éà„É™„Çπ„Éä„Éº„ÅÆÂâäÈô§
    this.removeAllEventListeners();

    // „Ç≥„É≥„Éù„Éº„Éç„É≥„ÉàË™≠„ÅøËæº„ÅøÊôÇÈñì„ÅÆ„ÇØ„É™„Ç¢
    this.componentLoadTimes.clear();

    console.log('‚úÖ MPA cleanup complete');
  }

  /**
   * ÂÖ®„Ç§„Éô„É≥„Éà„É™„Çπ„Éä„Éº„ÇíÂâäÈô§
   */
  removeAllEventListeners() {
    // „Ç´„Çπ„Çø„É†„Ç§„Éô„É≥„Éà„É™„Çπ„Éä„Éº„ÅÆÂâäÈô§
    const events = ['online', 'offline', 'error', 'unhandledrejection'];
    events.forEach((event) => {
      window.removeEventListener(
        event,
        this[`handle${event.charAt(0).toUpperCase() + event.slice(1)}`]
      );
    });
  }

  /**
   * MPA„ÅÆÁä∂ÊÖã„ÇíÂèñÂæó
   * @returns {Object} MPA„ÅÆÁä∂ÊÖã
   */
  getState() {
    return {
      isInitialized: this.isInitialized,
      currentPage: this.currentPage,
      initializationTime: this.initializationTime,
      errorCount: this.errorCount,
      componentLoadTimes: Object.fromEntries(this.componentLoadTimes),
      autoInitialize: this.autoInitialize,
      enablePerformanceMonitoring: this.enablePerformanceMonitoring,
    };
  }
}

// „Ç∞„É≠„Éº„Éê„É´ÂàùÊúüÂåñ„Ç§„É≥„Çπ„Çø„É≥„Çπ
const mpaInitializer = new MPAInitializer();

// DOMË™≠„ÅøËæº„ÅøÂÆå‰∫ÜÊôÇ„Å´ÂàùÊúüÂåñ„ÇíÂÆüË°å
document.addEventListener('DOMContentLoaded', async () => {
  console.log('üîÑ DOM loaded, initializing MPA...');
  await mpaInitializer.initialize();
});

// „Ç∞„É≠„Éº„Éê„É´„Å´ÂÖ¨Èñã
window.mpaInitializer = mpaInitializer;

export default mpaInitializer;
